<template>
  <!-- <div class="start" :class="{ end: 모달창열렸니 }"> // 이런식으로 표현할 수도 있음
    <Modal @removeModal="removeModal" :모달창열렸니="모달창열렸니" :product="product" />
  </div> -->
  <transition name="fade">
    <Modal @removeModal="removeModal" :모달창열렸니="모달창열렸니" :product="product" />
  </transition>

  <div class="menu">
    <a v-for="a in 메뉴들" :key="a">{{ a }}</a>
  </div>

  <Discount @removeDiscount="showDiscount = false" :showDiscount="showDiscount" />

  <button @click="priceSort">가격순정렬</button>&nbsp;&nbsp;&nbsp;
  <button @click="priceReSort">가격역순정렬</button>&nbsp;&nbsp;&nbsp;
  <button @click="titleSort">가나다순정렬</button>&nbsp;&nbsp;&nbsp;
  <button @click="sortBack">되돌리기</button>

  <Card @showModal="showModal($event)" v-for="(oneroom, i) in 원룸들" :key="i" :oneroom="oneroom" />
</template>
<script>
import data from "./assets/oneroom.js";
import Discount from "./Discount.vue";
import Modal from "./Modal.vue";
import Card from "./Card.vue"

export default {
  name: "App",
  data() {
    return {
      원룸들: [...data],
      원룸들오리지널: [...data],
      모달창열렸니: false,
      신고수: [0, 0, 0],
      메뉴들: ["Home", "Shop", "About"],
      products: ["역삼동원룸", "청호동원룸", "마포구원룸"],
      product: {},
      showDiscount: true
    };
  },
  methods: {
    increase(i) {
      this.신고수[i]++;
    },
    showModal(product) {
      this.product = product;
      this.모달창열렸니 = true;
    },
    removeModal() {
      this.모달창열렸니 = false;
    },
    priceSort() {
      this.원룸들.sort(function(a, b) {
        return a.price - b.price;
      });
    },
    priceReSort() {
      this.원룸들.sort(function(a, b) {
        return b.price - a.price;
      });
    },
    titleSort() {
      this.원룸들.sort(function(a, b) {
        let x = a.title.toLowerCase();
        let y = b.title.toLowerCase();
        if (x < y) {
          return -1;
        }
        if (x > y) {
          return 1;
        }
        return 0;
      });
    },
    sortBack() {
      this.원룸들 = [...this.원룸들오리지널];
    }
  },
  // created() {
  //   // 보통 여기서 서버에서 데이터 가져오는 코드 작성
  // },
  // mounted() {
  //   setTimeout(() => {
  //     this.showDiscount = false;
  //   }, 2000);
  // },
  components: {
    Discount: Discount,
    Modal: Modal,
    Card: Card
  },
};
</script>

