<template>
  <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <img :src="product.image" alt="" style="width: 100%" />
      <h4>{{ product.title }}</h4>
      <p>{{ product.content }}</p>
      <input @input="month = $event.target.value" :value="month">
      <p>{{ month }}개월 선택함 : {{ product.price * month }} 원</p>
      <button @click="removeModal">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Modal',
  data() {
    return {
      month: 1
    }
  },
  watch: {
    month(after, before) {
      const str_space = /\s/;
      const str_dot = /\./;
      if (str_space.exec(after)) {
        alert("공백을 사용할 수 없습니다");
        this.month = before;
        return;
      }
      if (str_dot.exec(after)) {
        alert("자연수를 입력해주세요");
        this.month = before;
        return;
      }
      if (isNaN(after)) {
        alert("숫자를 입력해주세요");
        this.month = before;
        return;
      }
      if (after > 12) {
        alert("13이상 입력하지 마세요");
        this.month = before;
      }
    }
  },
  props: {
    모달창열렸니: Boolean,
    product: Object
  },
  methods: {
    removeModal() {
      this.$emit('removeModal');
      this.month = 1;
    }
  },
  beforeUpdate() {
    if (this.month == 2) {
      alert('2개월은 너무 적음.. 안팝니다');
      this.month = 3;
    }
  }
}
</script>

