<template>
  <div class="discount" v-if="showDiscount == true">
    <h4>지금 결제하면 {{ disCountNum }}% 할인</h4>
  </div>
</template>

<script>
export default {
  name: 'Discount',
  data() {
    return {
      disCountNum: 30
    }
  },
  props: {
    showDiscount: Boolean
  },
  methods: {
    startDiscount() {
      const disCount = setInterval(() => {
        if (--this.disCountNum == 0) {
          clearInterval(disCount);
          this.$emit('removeDiscount');
        }
      }, 1000);
    }
  },
  mounted() {
    this.startDiscount();
  }
}
</script>

